import React, {Component} from 'react';
import closeImg from './close.png';
import Switch from './components/Switch/index';

import './App.css';

class Home extends Component {

    constructor(props) {
        super(props);
        this.state = {
            checkedList: ['China', 'Math', 'English'],//checkbox选中的值
            inputValue: '',//input控件的值
            selectValue: '橘子',//select控件值
            switchValue:false,//switch控件值
            sliderValue:0,//slider控件的值
            showClose: false,//input是否展示关闭按钮
            showRight:false//右侧数据是否展示
        };
        this.hideClose = this.hideClose.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleCheckChange = this.handleCheckChange.bind(this);
        this.handleSelectChange = this.handleSelectChange.bind(this);
        this.handleSliderChange = this.handleSliderChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleInputChange(event) {
        const value = event.target.value;
        this.setState({inputValue: value});
        if (value) {
            this.setState({showClose: true})
        }
    }

    handleSelectChange(event) {
        this.setState({selectValue: event.target.value});
    }

    delItem(arr, toDelItem) {
        const index = arr.findIndex(item => item === toDelItem);
        arr.splice(index, 1);
        return arr
    }

    handleCheckChange(event) {
        const target = event.target;
        const {checkedList} = this.state;
        const newArray = checkedList.includes(target.name)
            ? this.delItem(checkedList, target.name)
            : [...checkedList, target.name];
        this.setState({checkedList: newArray})
    }

    handleSliderChange(event){
        this.setState({sliderValue:event.target.value})
    }

    hideClose() {
        this.setState({inputValue: '', showClose: false});
    }

    handleSubmit(event) {
        event.preventDefault();
        let x = document.getElementById("snackbar");
        x.className = "show";
        const that = this;
        setTimeout(function() {
            x.className = x.className.replace("show", "");
            that.setState({showRight:true})
        }, 3000);
    }

    renderCheckbox() {
        const {checkedList} = this.state;
        return (
            <div className={'title'}>
                <label>语文<input name={'China'} type={'checkbox'} onChange={this.handleCheckChange}
                                checked={checkedList.includes('China')}></input></label><br/>
                <label>数学<input name={'Math'} type={'checkbox'} onChange={this.handleCheckChange}
                                checked={checkedList.includes('Math')}></input></label><br/>
                <label>英语<input name={'English'} type={'checkbox'} onChange={this.handleCheckChange}
                                checked={checkedList.includes('English')}></input></label>
            </div>
        )
    }

    renderSelect() {
        const {selectValue} = this.state;
        return (
            <div className={'title'} >
                <select value={selectValue} onChange={this.handleSelectChange} style={{width:'100px',height:'30px',margin:'20px auto'}}>
                    <option value={'orange'}>橘子</option>
                    <option value={'apple'}>苹果</option>
                    <option value={'banana'}>香蕉</option>
                </select>
            </div>
        )
    }

    renderInput() {
        const {inputValue, showClose} = this.state;
        return (
            <div className={'title'} style={{display:'flex',alignItems:'center',justifyContent:'center',margin:'20px auto'}}>
                <input  style={{width:'200px',height:'30px',outline:'none',border:0,borderBottom:'2px solid blue'}} value={inputValue} type={"text"} autoComplete="off"
                       onChange={this.handleInputChange}
                       name={"name"} placeholder={"输入内容后出现清除按钮"}/>
                {
                    showClose ? <img alt={'删除'} className={'close_img'} onClick={this.hideClose} src={closeImg}/> : null
                }
            </div>
        )
    }

    renderSwitch(){
        const { switchValue } = this.state;
        return (
            <div>
                <header className='App-header'>
                    <Switch
                        checked={switchValue}
                        onChangeSwitch={(checked)=>{ console.log('父组件',checked); this.setState({switchValue:checked})}}
                    />
                </header>
            </div>
        )
    }

    renderSlider(){
        const { sliderValue } = this.state;
        return (
            <input type="range" value={sliderValue} onChange={this.handleSliderChange} />
        )
    }

    renderForm() {
        return (
            <form onSubmit={this.handleSubmit} style={{width: '50%'}}>
                {this.renderCheckbox()}
                {this.renderSelect()}
                {this.renderSlider()}
                {this.renderInput()}
                {this.renderSwitch()}
                <input style={{marginTop: '20px',width: '100px',height: '30px'}} type={"submit"} value={"提交"}/>
            </form>
        )
    }

    renderInfo() {
        const {inputValue, selectValue, checkedList,switchValue,sliderValue} = this.state;
        console.log('switchValue',switchValue);
        return (
            <div >
                <div>
                    <text>复选checkbox：</text>
                    <text>{checkedList.includes('China')?'语文':''}</text>
                    <text>{checkedList.includes('Math')?'数学':''}</text>
                    <text>{checkedList.includes('English')?'英语':''}</text>
                </div>
                <div style={{margin:'50px auto'}}>
                    <text>下拉选择select：</text>
                    <text>{selectValue}</text>
                </div>
                <div>
                    <text>slide进度条：</text>
                    <text>{sliderValue}</text>
                </div>
                <div style={{margin:'50px auto'}}>
                    <text>输入框input：</text>
                    <text>{inputValue}</text>
                </div>
                <div>
                    <text>开关switch：</text>
                    <text>{switchValue?'true':'false'}</text>
                </div>
            </div>
        )
    }

    render() {
        const { showRight } = this.state;
        return (
            <div className={'container'}>
                <div style={{display:'flex',width:'100%'}}>
                    {this.renderForm()}
                    <div style={{width:'50%'}}>
                        {
                            showRight?this.renderInfo():null
                        }
                    </div>
                </div>
                <div id="snackbar">提交成功，即将更新后侧..</div>
            </div>
        );
    }
}

export default Home;